@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
* {
  margin: 0;
  padding: 0;
}
.cards-wrapper {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
}

.card {
    width: 200px;
    margin: 20px;
    border: 3px solid transparent;
    -webkit-border-image:  -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
            border-image:  linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    padding: 30px;
    border-image-slice: 1;
    border-image-width: 1;
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.bookmark {
    position: absolute;
    top: -1px;
    right: 5px;
    color: #e53788;
}
.app-wrapper {
  width: 100%;
  height: 100vh;
}

.app-header {
  min-height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
          animation: Gradient 15s ease infinite;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 45px;
  color: #fff;
}

.quote-zone {
  font-size: 15px;
  max-width: 400px;
  text-align: center;
  min-height: 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
}

button {
  border: none;
  background: transparent;
  padding: 10px;
  cursor: pointer;
  color: #ffff;
}

.seperator-wrapper {
  width: 100%; 
}

.seperator {
  width: 3000px;
  height: 5px;
  -webkit-animation: rotate 3s infinite linear;
          animation: rotate 3s infinite linear;
}

.gradient {
  background: -webkit-linear-gradient(left,  rgba(48,255,144,1) 0%,rgba(237,45,237,1) 25%,rgba(201,152,38,1) 50%,rgba(48,255,230,1) 75%,rgba(48,255,144,1) 100%);
  background: linear-gradient(to right,  rgba(48,255,144,1) 0%,rgba(237,45,237,1) 25%,rgba(201,152,38,1) 50%,rgba(48,255,230,1) 75%,rgba(48,255,144,1) 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#30ff90', endColorstr='#30ff90',GradientType=1 ); /* IE6-9 */
}

@-webkit-keyframes rotate {
  from {
    background-position: -3000px;
  }
  to { 
    background-position: 0px;
  }
}

@keyframes rotate {
  from {
    background-position: -3000px;
  }
  to { 
    background-position: 0px;
  }
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}
